const state = {
  //baseLink: 'http://localhost:3001',
  baseLink: "https://jmrsquared.com/api/coportal",
  schoolAdded: false,
  school:school,
  defaultProfilePic:
    "data:image/png;base64,"
};

const mutations = {
  changeSchool(state, school) {
    state.baseLink = state.baseLink + "/" + school;
    state.school = school;
    state.schoolAdded = true;
  }
};

const actions = {
  changeSchool: ({ commit }) => commit("changeSchool")
};

export default {
  state,
  mutations,
  actions
};



